/*image layers */

/*gobierno local por Poblacion */
const imageLayersPorGobiernoLocalPorPoblacion =
  layersPorGobiernoLocalPorPoblacion.map(
    ({ title, layer }) =>
      new ImageLayer({
        title,
        visible: false,
        source: new ImageWMS({
          url: `${urlEstadistica}/censo2022/wms`,
          params: { LAYERS: layer, grupo: "censo2022" },
          serverType: "geoserver",
          crossOrigin: "Anonymous",
        }),
      })
  );
