import "./css/style.css";
import { Map, View } from "ol";
//CONTROLERS
import Zoom from "ol/control/Zoom";
import ScaleLine from "ol/control/ScaleLine";
import Rotate from "ol/control/Rotate";
import Attribution from "ol/control/Attribution";
import FullScreen from "ol/control/FullScreen";
import MousePosition from "ol/control/MousePosition.js";
import ZoomSlider from "ol/control/ZoomSlider";
import ZoomToExtent from "ol/control/ZoomToExtent";
import Refresh from "./botones/refreshButton";
import bCapaActiva from "./botones/botonCapaActiva";
import MeasureControl from "./botones/botonMeasure";
import MeasureLine from "./botones/botonMeasureLine";
import Dibujo from "./botones/botonDibujo.js";

import Circulo from "./botones/botonDibujoCirculo.js";
import Texto from "./botones/botonTexto.js";

//layers
import { fuentesExternas } from "./capas_layers/fuentes_externas";
import { marcoGeoestadistico } from "./capas_layers/marcoGeoestadistico";
import { censo2001 } from "./capas_layers/censo2001";
import { censo2010 } from "./capas_layers/censo2010";
import { censo2022 } from "./capas_layers/censo2022";
import { baseLayers } from "./capas_layers/baseLayers";
import { cartografiaBase } from "./capas_layers/cartografiaBase";
//extensiones
import LayerSwitcher from "./layerSwitcher.js";
import Popup from "ol-popup";
import { createStringYX } from "./createStringYX";
import { onLayerVisibilityChange } from "./cambioCapa";
import { imprimir } from "./impresion";
import Impresion from "./botones/botonImpresion";
import Ayuda from "./botones/botonAyuda";
import { buscadorLocalidades } from "./buscadorLocalidades";
import { cambioOrdenCapasActivas } from "./ordenCapasActivas";
import { popUP } from "./popUp";

import DibujoLinea from "./botones/botonDibujoLinea.js";
import Goma from "./botones/botonGoma.js";

const scaleLine = new ScaleLine({ bar: true, text: true }); // Control de escala

//

const map = new Map({
  //controles
  controls: [
    new Zoom({ zoomInTipLabel: "Acercar", zoomOutTipLabel: "Alejar" }), // Control de zoom
    new Rotate(), // Control de rotacin
    new Attribution(), // Control de atribucin
    new FullScreen({ tipLabel: "Pantalla completa" }),
    new bCapaActiva(),
    //
    new MeasureControl({ onActivate: desactivarControlesActivos }),
    new MeasureLine({ onActivate: desactivarControlesActivos }),
    new Impresion({ tipLabel: "Imprimir mapa" }),
    new Ayuda(),
    //
    new Dibujo({ onActivate: desactivarControlesActivos }),
    new Circulo({ onActivate: desactivarControlesActivos }),
    new DibujoLinea({ onActivate: desactivarControlesActivos }),
    new Texto({ onActivate: desactivarControlesActivos }),
    new Goma(),
    new MousePosition({
      coordinateFormat: createStringYX(4),
      projection: "EPSG:4326",
      className: "custom-mouse-position",
      target: document.getElementById("mouse-position"),
    }),
    new Refresh(),
    new ZoomToExtent({
      label: " ",
      tipLabel: "Zoom inicial",
      //tucuman
      // (-7579297.1525,-28988881.2432)
      extent: [-7685524.9259, -3260079.4028, -6865275.1616, -2994070.9759],
      //gsmt
      //extent: [-7278753.9878, -3110243.0708, -7240359.4153,-3092280.6960]
      //x- izq arriba, y- izq abajo, x-derecha abajo, y- derecha arriba
      //extent: [-11243808.051695308, 4406397.202710291, -4561377.290892059, 6852382.107835932]
    }),
  ],
  target: "map",
  //layers
  layers: [
    baseLayers,
    marcoGeoestadistico,
    censo2022,
    censo2010,
    censo2001,
    cartografiaBase,
    fuentesExternas,
  ],
  //vista
  view: new View({
    center: [-7269760.5264, -3116502.3825],
    zoom: 8.5,
  }),
});
map.addControl(scaleLine);

function desactivarControlesActivos() {
  map.getControls().forEach((ctrl) => {
    if (ctrl.active) {
      ctrl.desactivar();
    }
  });
}
//Interacciones con el mapa
//interaccin del link guarda el estado actual al actualizar la pgina
//map.addInteraction(new Link());

//define el switcher y agrega el control al mapa
const layerSwitcher = new LayerSwitcher({
  activationMode: "click",
  tipLabel: "Capas",
  groupSelectStyle: "none",
  reverse: false,
});

map.addControl(layerSwitcher);

//popup y agrega al mapa
const popup = new Popup();
map.addOverlay(popup);

// Funciones

//Eventos
//Evento de cambio de capa
//Evento de cada capa
map.getAllLayers().forEach((layer) => {
  layer.on("change:visible", (event) =>
    onLayerVisibilityChange(event, map.getView())
  );
});

//evento del click popup
map.on("singleclick", async (evt) => popUP(evt, map, popup));

